import React, { Component } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { Button, Card, CardBody, CardFooter, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap';

class Register extends Component {
  state={
    identification: '',
    name: '',
    email: '',
    password: '',
    Rpassword: '',
    ok:false,
    error:false,
  }



  valida = (campo) => {
    return (valor) => {
      this.setState({
        [campo]: valor.target.value,
      })
    }
  }
  componentWillUnmount = () => this.setState({ok:false})
  login=(e)=>{
    e.preventDefault();
    console.log(this.state)
    const {email, password, Rpassword, name, identification} = this.state;
        console.log(email, password)
        if(password !== Rpassword) {
          alert('las contraseñas no coinciden');
          return this.setState({Rpassword: '', password: ''});
        }
        return fetch('https://peerpeel.herokuapp.com/v1/client',{
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                email,
                name,
                identification,
                password,
            })
        })
        .then(res => res.json())
        .then(json =>{
          const id = json['data'].id;
          localStorage.setItem('uid',id);
          console.log(json);
          //alert(id);
          this.setState({ok: true})
          return (<Redirect push to='/forms' />);
        })
        .catch(err => this.setState({error:true}));
      }
  render() {
    const { ok, error } = this.state;
    if(ok){
      return (<Redirect push to='/forms' />);
    }
    return (
      <div className="app flex-row align-items-center">
        <Container>
          <Row className="justify-content-center">
            <Col md="9" lg="7" xl="6">
              <Card className="mx-4">
                <CardBody className="p-4">
                  <Form>
                    <img src={require('../../../assets/img/LogoColor.png')} style={{width:'100%'}}/>
                      <br/><br/>
                    <p className="text-muted">Create your account</p>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-user"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="text" placeholder="Username" autoComplete="username"  onChange={this.valida('name')}/>
                    </InputGroup>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>#</InputGroupText>
                      </InputGroupAddon>
                      <Input type="number" placeholder="Identification" autoComplete="identification"  onChange={this.valida('identification')}/>
                    </InputGroup>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>@</InputGroupText>
                      </InputGroupAddon>
                      <Input type="text" placeholder="Email" autoComplete="email"  onChange={this.valida('email')}/>
                    </InputGroup>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-lock"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="password" placeholder="Password" autoComplete="new-password"  onChange={this.valida('password')} />
                    </InputGroup>
                    <InputGroup className="mb-4">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-lock"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="password" placeholder="Repeat password" autoComplete="new-password"  onChange={this.valida('Rpassword')}/>
                    </InputGroup>
                    <Button color="success" onClick={(e)=>this.login(e)} block>Create Account</Button>
                    <Link to="/login">
                        <Button  color="link" className="px-0">I already have an account!</Button>
                      </Link>
                  </Form>
                </CardBody>
                <CardFooter className="p-4">
                  <Row>
                    <Col xs="12" sm="6">
                      <Button className="btn-facebook mb-1" block><span>facebook</span></Button>
                    </Col>
                    <Col xs="12" sm="6">
                      <Button className="btn-twitter mb-1" block><span>twitter</span></Button>
                    </Col>
                  </Row>
                </CardFooter>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Register;
